import { Route, Redirect } from "react-router-dom";
import "./App.css";
import Dashboard from "./component/Dashboard";
// import DashboardTL from "./component/DashboardTL"
import DashboardFse from "./component/DashboardFse";
import Login from "./component/Login";
import DashboardTl from "./component/DashoardTl";
import { useState, useEffect } from "react";

function App() {
  const [loggedin, setLogin] = useState(false); //initial flase
  const [tlUserId, setTlUserId] = useState("");
  const [fseUserId, setFseUserId] = useState("");

  useEffect(() => {
    const key = localStorage.getItem("asm_token");
    const email_id = localStorage.getItem("asm_token_email");
    if (key && email_id) {
      setLogin(true); //success
    } else {
      setLogin(false); //reload
    }
  }, []);

  return (
    <div className="app">
      {loggedin ? (
        <>
            <Redirect to="/dashboard" />
          <Route path="/dashboard">
            <Dashboard setLogin={setLogin} setTlUserId={setTlUserId} />
          </Route>
          <Route path="/dashboard_tl">
            <DashboardTl tlUserId={tlUserId} setFseUserId={setFseUserId} />
          </Route>
          {/* <Route path="/dashboard_tl/1" component={DashboardTL} /> */}
          <Route path="/dashboard_fse">
            <DashboardFse fseUserId={fseUserId} />
          </Route>
        </>
      ) : (
        <>
          <Login setLogin={setLogin} />
        </>
      )}
    </div>
  );
}

export default App;
